.header {
  width: 100%;
  box-shadow: 1px 3px 25px var(--green-shadow);
  border-bottom: .5px solid var(--light-accent);
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.container {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header-logo {
  color: var(--color-text);
  padding: 5px 0 2px;
  font-size: 12px;
  line-height: 1.1;
}

.header-logo:hover, .header-logo:active {
  color: var(--accent);
  border-bottom: .5px solid var(--light-accent);
  transition: all .5s;
  transform: scale(1.3);
}

.header-nav {
  display: none;
}

.mobile-menu-btn {
  width: 24px;
  height: 24px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.mobile-menu-pic {
  width: 24px;
  height: 24px;
  fill: var(--color-text);
}

.mobile-menu-pic:hover {
  fill: var(--accent);
  transition: all .5s;
  transform: scale(1.2);
}

.mob-modal {
  z-index: 3;
  width: 100%;
  height: 100vh;
  background-color: var(--bg-color-3);
  box-shadow: 0 15px 20px var(--mob-top-shadow);
  border-radius: 0 0 5px 5px;
  flex-direction: column;
  gap: 150px;
  transition: transform 1.2s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0%;
  right: 0%;
  overflow: visible;
  transform: translate(0);
}

.mob-top-menu {
  box-shadow: 0 3px 15px var(--mob-shadow);
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

.mob-logo {
  color: var(--bg-color);
}

.mob-logo:hover {
  color: var(--bg-color);
  border-bottom: .5px solid var(--light-accent);
}

.mob-x-btn {
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

.mob-x-pic {
  width: 32px;
  height: 32px;
  stroke: var(--bg-color);
}

.mob-x-btn:hover, .mob-x-btn:active {
  transition: all .5s;
  transform: scale(1.3);
}

.mob-list {
  flex-direction: column;
  justify-content: center;
  gap: 24px;
  margin: 0 auto;
  display: flex;
}

.mob-item {
  text-transform: uppercase;
  color: var(--cream);
  color: var(--bg-color);
  padding: 5px 2px;
  font-size: 15px;
  line-height: 1.1;
}

.mob-item:hover, .mob-item:active {
  color: var(--bg-color);
  border-bottom: .5px solid var(--light-accent);
  transition: all .5s;
  transform: scale(1.3);
}

.appear {
  transform: translateY(-120%);
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }

  .header-logo {
    font-size: 15px;
  }

  .mobile-menu-btn {
    display: none;
  }

  .header-nav {
    display: flex;
  }

  .header-list {
    gap: 40px;
    display: flex;
  }

  .header-item {
    text-transform: uppercase;
    color: var(--color-text);
    border-bottom: 1px solid rgba(0, 0, 0, 0);
    padding: 5px 2px 2px;
    font-size: 15px;
    line-height: 1.1;
  }

  .header-item:hover {
    color: var(--accent);
    border-bottom: .5px solid var(--light-accent);
    transition: all .5s;
    transform: scale(1.3);
  }

  .header-link {
    padding: 20px 2px;
  }

  .mob-modal {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.hero {
  background-color: var(--bg-color);
  padding: 55px 0;
}

.hero-container {
  flex-direction: column;
  gap: 10px;
  padding: 0 15px;
  display: flex;
}

.hero-text {
  flex-direction: column;
  gap: 10px;
  margin: 0 auto;
  display: flex;
}

.greeting-span {
  color: var(--color-text);
  justify-content: center;
  font-size: 15px;
  line-height: 1.1;
  display: flex;
}

.greeting {
  color: var(--white);
  justify-content: center;
  font-size: 43px;
  line-height: 1.17;
  display: flex;
}

.hero-descr {
  color: var(--color-text);
  justify-content: center;
  margin-top: -10px;
  font-size: 20px;
  display: flex;
}

.hero-img {
  border: .5px solid var(--light-accent);
  border-radius: 50%;
  box-shadow: 0 0 20px 2px rgba(255, 255, 255, .28);
}

.welcome-descr {
  border: .5px solid var(--light-accent);
  padding: 10px 2px;
  font-size: 13px;
  line-height: 1.1;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }

  .hero {
    padding: 75px 0;
  }

  .hero-container {
    gap: 20px;
    padding: 0 25px;
  }

  .top-part {
    flex-direction: row;
    gap: 10px;
    display: flex;
  }

  .hero-img {
    width: 358px;
  }

  .welcome-descr {
    width: 270px;
    margin-top: -240px;
    margin-left: -370px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

